<template>
  <div style="width: 80%; margin: 20px auto">
    <div class="search">
      <el-input v-model="username" placeholder="请输入角色名"></el-input>
      <el-button @click="search()">查询</el-button>
    </div>
    <el-table ref="filterTable" :data="roleList" style="width: 100%">
      <el-table-column align="center" prop="picture" label="头像" width="100">
        <el-image
          style="
            width: 60px;
            height: 60px;
            border-radius: 50px;
            overflow: hidden;
          "
          src="https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg"
          :preview-src-list="roleList"
        >
        </el-image>
      </el-table-column>
      <el-table-column
        align="center"
        prop="username"
        label="用户"
        width="180"
      ></el-table-column>
      <el-table-column
        align="center"
        prop="sex"
        label="性别"
        width="180"
      ></el-table-column>
      <el-table-column
        align="center"
        prop="phone"
        label="电话"
        width="180"
      ></el-table-column>
      <el-table-column align="center">
        <template slot="header" slot-scope=""> 角色 </template>
        <template slot-scope="scope">
          <el-button v-if="judgePermission(scope.$index) == 0" size="mini"
            >用户</el-button
          >
          <el-button
            v-if="judgePermission(scope.$index) == 1"
            size="mini"
            type="primary"
            >管理员</el-button
          >
          <el-button
            v-if="judgePermission(scope.$index) == 2"
            size="mini"
            type="success"
            >售票员</el-button
          >
          <el-button
            v-if="judgePermission(scope.$index) == 3"
            size="mini"
            type="info"
            >业务员</el-button
          >
        </template>
      </el-table-column>
      <el-table-column align="center">
        <template slot="header" slot-scope=""> 权限管理 </template>
        <template slot-scope="scope">
          <el-button
            v-if="judgePermission(scope.$index) == 0"
            size="mini"
            type="primary"
            @click="setAdministrator(scope.$index)"
            >设为管理员</el-button
          >
          <el-button
            v-if="judgePermission(scope.$index) == 1"
            size="mini"
            @click="setUser(scope.$index)"
            >改为用户</el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  name: "RolesManagement",
  data() {
    return {
      username: "",
      roleList: [],
    };
  },
  async created() {
    const { data: res } = await this.$http.get("/user/usersInfo", {
      params: {
        token: window.localStorage.getItem("token"),
      },
    });
    this.roleList = res.value;
    console.log(this.roleList);
  },
  methods: {
    async search() {
      const { data: res } = await this.$http.get("/user/getUserByName", {
        params: {
          token: window.localStorage.getItem("token"),
          name: this.username,
        },
      });
      this.roleList = res.value;
    },
    handleEdit(index, row) {
      console.log(index, row);
    },
    async setAdministrator(index) {
      const { data: rest } = await this.$http.post("/user/changeUserLevel", {
        token: window.localStorage.getItem("token"),
        // id: window.localStorage.getItem("id"),
        id: this.$data.roleList[index].id,
        level: 1,
      });
      console.log(rest);
      const { data: res } = await this.$http.get("/user/usersInfo", {
        params: {
          token: window.localStorage.getItem("token"),
        },
      });
      this.roleList = res.value;
      console.log(res);
    },
    async setUser(index) {
      const { data: rest } = await this.$http.post("/user/changeUserLevel", {
        token: window.localStorage.getItem("token"),
        id: this.$data.roleList[index].id,
        level: 0,
      });
      const { data: res } = await this.$http.get("/user/usersInfo", {
        params: {
          token: window.localStorage.getItem("token"),
        },
      });
      this.roleList = res.value;
    },
    // 判断用户权限
    judgePermission(index) {
      if (this.$data.roleList[index].permissionLevel == "audience") {
        return 0;
      } else if (this.$data.roleList[index].permissionLevel == "admin") {
        return 1;
      } else if (this.$data.roleList[index].permissionLevel == "conductor") {
        return 2;
      } else if (
        this.$data.roleList[index].permissionLevel == "ticketInspector"
      ) {
        return 3;
      } else if (this.$data.roleList[index].permissionLevel == "Clerk") {
        return 5;
      }
    },
    formatter(row, column) {
      return row.address;
    },
    filterPermission(value, row) {
      return row.permissionLevel === value;
    },
    filterHandler(value, row, column) {
      const property = column["property"];
      return row[property] === value;
    },
  },
};
</script>
<style scoped>
.search {
  width: 80%;
  margin: 40px auto 10px;
}
.search .el-input {
  width: 200px;
  margin-right: 20px;
}
</style>