// 票款管理
<template>
  <div>
    <div class="top">
      <div class="time">
        <span>{{ this.todayDate }}</span>
        &nbsp;&nbsp;&nbsp;
        <span style="color: green">已更新</span>
      </div>
    </div>
    <!-- 展示 -->
    <div class="salesShow">
      <ul>
        <li>
          <el-tag class="l">销售额：</el-tag
          ><el-tag effect="dark">{{ this.salesData.boxOfficeQuantity }}</el-tag>
        </li>
        <li>
          <el-tag class="l">票数：</el-tag
          ><el-tag effect="dark">{{ this.salesData.boxOfficeReceipts }}</el-tag>
        </li>
        <li>
          <el-tag class="l">退票数：</el-tag
          ><el-tag effect="dark">{{
            this.salesData.numberOfScreenings
          }}</el-tag>
        </li>
        <li>
          <el-tag class="l">折扣：</el-tag
          ><el-tag effect="dark">{{ this.salesData.numberViewers }}</el-tag>
        </li>
        <li>
          <el-tag class="l">客户数量：</el-tag
          ><el-tag effect="dark">{{ this.salesData.refunds }}</el-tag>
        </li>
        <li>
          <el-tag class="l">支付方式：</el-tag
          ><el-tag effect="dark">微信支付</el-tag>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  name: "TicketMoneyManagement",
  data() {
    return {
      todayDate: "",
      salesData: {},
    };
  },
  async created() {
    var temp = new Date();
    this.todayDate += temp.getFullYear() + "-";
    this.todayDate += temp.getMonth() + 1;
    this.todayDate += "-" + temp.getDate();
    const { data: res } = await this.$http.get(
      "/FinancialStatistics/getTotalSales",
      {
        params: {
          token: window.localStorage.getItem("token"),
        },
      }
    );
    this.salesData = res.value;
    console.log(res);
  },
  methods: {},
};
</script>
<style scoped>
.top {
  margin-top: 20px;
}
.top .time {
  font-size: 20px;
}
.salesShow {
  width: 400px;
  margin: 10px auto;
}
.salesShow ul li {
  margin-bottom: 10px;
  font-size: 16px;
}
.el-tag {
  width: 250px;
  height: 60px;
  line-height: 60px;
  border: 0;
  border-radius: 0;
  font-size: 16px;
}
.l {
  width: 100px;
  margin-right: 1px;
}
</style>