<template>
  <div>
    <div class="salesShow" style="margin-top: 50px">
      <ul>
        <li>
          <el-tag class="l">演出时间：</el-tag
          ><el-tag effect="dark">{{ this.salesData.time }}</el-tag>
        </li>
        <li>
          <el-tag class="l">票数：</el-tag
          ><el-tag effect="dark">{{ this.salesData.ticketNumber }}</el-tag>
        </li>
        <li>
          <el-tag class="l">销售额：</el-tag
          ><el-tag effect="dark">{{ this.salesData.sales }}</el-tag>
        </li>
        <li>
          <el-tag class="l">退票数：</el-tag
          ><el-tag effect="dark">{{ this.salesData.refunds }}</el-tag>
        </li>
        <li>
          <el-tag class="l">观影人数：</el-tag
          ><el-tag effect="dark">{{ this.salesData.numberViewers }}</el-tag>
        </li>
        <li>
          <el-tag class="l">支付方式：</el-tag
          ><el-tag effect="dark">微信支付</el-tag>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      salesData: {},
    };
  },
  async created() {
    const { data: res } = await this.$http.get(
      "/FinancialStatistics/getVideoPlanSales",
      {
        params: {
          token: window.localStorage.getItem("token"),
          planId: window.localStorage.getItem("planId"),
        },
      }
    );
    this.salesData = res.value;
    console.log(res);
  },
  methods: {},
};
</script>
<style scoped>
.salesShow ul li {
  margin-bottom: 10px;
  font-size: 16px;
}
.el-tag {
  width: 250px;
  height: 60px;
  line-height: 60px;
  border: 0;
  border-radius: 0;
  font-size: 16px;
}
</style>