// 未完成
<template>
  <div>
    <div class="unsaved">
      <div class="updateForm">
        <el-form :model="personalInformation" status-icon label-width="100px">
          <h3 class="Title">个人信息</h3>
          <el-form-item label="昵称" prop="personalInformation.username">
            <el-input
              v-model="personalInformation.username"
              :disabled="dis"
            ></el-input>
          </el-form-item>
          <el-form-item label="性别" prop="personalInformation.sex">
            <el-input
              v-model="personalInformation.sex"
              :disabled="dis"
            ></el-input>
          </el-form-item>
          <el-form-item label="生日" prop="personalInformation.birthday">
            <el-input
              v-model="personalInformation.birthday"
              :disabled="dis"
            ></el-input>
          </el-form-item>
          <el-form-item label="手机号" prop="personalInformation.phone">
            <el-input
              v-model="personalInformation.phone"
              :disabled="dis"
            ></el-input>
          </el-form-item>
          <el-form-item label="爱好" prop="personalInformation.hobbies">
            <el-input
              v-model="personalInformation.hobbies"
              :disabled="dis"
            ></el-input>
          </el-form-item>
          <el-form-item label="个性签名" prop="personalInformation.description">
            <el-input
              v-model="personalInformation.description"
              :disabled="dis"
            ></el-input>
          </el-form-item>
          <el-button class="one" type="primary" @click="updateForm()"
            >修改</el-button
          >
          <el-button type="primary" @click="saveForm()">保存</el-button>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "BasicInformation",
  data() {
    return {
      // 是否为第一次登录
      firstCome: false,
      dis: true,
      personalInformation: {
        phone: "1312432355",
        username: "admin",
        sex: "男",
        birthday: "2002-12-12",
        hobbies: "吃饭、睡觉",
        description: "我就是我，不一样的烟火",
      },
    };
  },
  async created() {
    // 获取用户数据请求
    const { data: res } = await this.$http.get("/user/userInfo", {
      params: {
        token: window.localStorage.getItem("token"),
        id: window.localStorage.getItem("id"),
      },
    });
    // console.log(res.value);
    this.personalInformation = res.value;
  },
  methods: {
    updateForm() {
      this.dis = false;
    },
    async saveForm() {
      this.dis = true;
      const { data: res } = await this.$http.post("/user/changeUserInfo", {
        token: window.localStorage.getItem("token"),
        value: this.personalInformation,
      });
      // 保存请求
      // 再次获取数据请求
      const { data: rest } = await this.$http.get("/user/userInfo", {
        params: {
          token: window.localStorage.getItem("token"),
          id: window.localStorage.getItem("id"),
        },
      });
      // console.log(res.value);
      this.personalInformation = rest.value;
    },
  },
};
</script>
<style scoped>
.updateForm {
  width: 350px;
  height: 500px;
  margin: 10px auto;
  /* margin: 10px 100px; */
  border-radius: 15px;
  background-clip: padding-box;
  padding: 15px 35px 15px 35px;
  box-sizing: border-box;
  background: aliceblue;
  border: 1px solid blueviolet;
  box-shadow: 0 0 25px #f885ff;
}
.el-button {
  float: left;
  width: 100px;
  text-align: center;
  border-radius: 5px;
  margin: 10px 0;
}
.one {
  margin-left: 50px;
}
.unsaved .el-form {
  float: left;
}
h3 {
  margin: 10px auto;
  text-align: center;
}
.show {
  width: 60%;
  /* margin: 0 auto; */
  padding: 20px;
}
.show .el-descriptions-item {
  padding: 20px;
  margin: 20px;
}
</style>