// 销售统计2
<template>
  <div>
    <div class="search">
      <el-input v-model="VideoName" placeholder="请输入剧目名"></el-input>
      <el-button @click="search()">查询</el-button>
    </div>
    <div class="top">
      <div class="time">
        <span>{{ this.todayDate }}</span>
        &nbsp;&nbsp;&nbsp;
        <span style="color: green">已更新</span>
      </div>
    </div>
    <!-- 销售展示 -->
    <div class="salesShow">
      <ul>
        <li>
          <el-tag class="l">销售额：</el-tag
          ><el-tag effect="dark">{{ this.salesData.sales }}</el-tag>
        </li>
        <li>
          <el-tag class="l">票数：</el-tag
          ><el-tag effect="dark">{{ this.salesData.ticketNumber }}</el-tag>
        </li>
        <li>
          <el-tag class="l">退票数：</el-tag
          ><el-tag effect="dark">{{ this.salesData.refunds }}</el-tag>
        </li>
        <li>
          <el-tag class="l">演出计划数：</el-tag
          ><el-tag effect="dark">
            {{ this.salesData.planId.length }}
          </el-tag>
        </li>
        <li>
          <el-tag class="l">观影人数：</el-tag
          ><el-tag effect="dark">{{ this.salesData.numberViewers }}</el-tag>
        </li>
        <li>
          <el-tag class="l">支付方式：</el-tag
          ><el-tag effect="dark">微信支付</el-tag>
        </li>
      </ul>
    </div>
    <!-- 查看演出计划对应的信息统计 -->
    <div class="btn">
      <el-button>点此查看演出计划对应的销售统计信息</el-button>
    </div>
    <!-- 列表展示演出计划id -->
    <div class="list">
      <ul
        v-for="(item, index) in planId"
        :key="index"
        @click="turnToTongJi(item)"
      >
        <li>
          查看&nbsp;&nbsp;{{ VideoName }}&nbsp;&nbsp; 的演出计划{{ index + 1 }}
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  name: "SalesStatisticsTwo",
  data() {
    return {
      VideoName: "",
      todayDate: "",
      statistics: [],
      planId: [],
      salesData: {
        sales: "0",
        ticketNumber: "0",
        refunds: "0",
        planId: [],
        numberViewers: "0",
      },
    };
  },
  created() {
    var temp = new Date();
    this.todayDate += temp.getFullYear() + "-";
    this.todayDate += temp.getMonth() + 1;
    this.todayDate += "-" + temp.getDate();
  },
  methods: {
    async search() {
      const { data: res } = await this.$http.get(
        "/FinancialStatistics/getVideoSales",
        {
          params: {
            token: window.localStorage.getItem("token"),
            VideoName: this.VideoName,
          },
        }
      );
      console.log(res);
      this.salesData = res.value;
      console.log(this.salesData);
      this.planId = res.value.planId;
    },
    async turnToTongJi(item) {
      console.log(item);
      window.localStorage.setItem("planId", item);
      this.$router.push("/TongJi");
    },
  },
};
</script>
<style scoped>
.search {
  width: 80%;
  margin: 40px auto 0;
}
.search .el-input {
  width: 200px;
  margin-right: 20px;
}
.top {
  margin-top: 20px;
}
.top .time {
  font-size: 20px;
}
.salesShow {
  width: 60%;
  margin: 10px auto;
  text-align: center;
  padding: 20px;
}
/*  */
.show {
  width: 60%;
  margin: 0 auto;
}
.show table tr {
  width: 100%;
}
.show table th,
.show table td {
  text-align: center;
  width: 100px;
  height: 40px;
}
.show table td button {
  width: 48px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  margin-right: 2px;
}
.salesShow ul li {
  margin-bottom: 10px;
  font-size: 16px;
}
.el-tag {
  width: 250px;
  height: 60px;
  line-height: 60px;
  border: 0;
  border-radius: 0;
  font-size: 16px;
}
</style>