// 未完成
<template>
  <div style="width: 100%">
    <div class="updateForm">
      <el-form :model="order" status-icon label-width="100px">
        <h3 class="Title">订单信息</h3>
        <el-form-item label="订单Id" prop="order.id">
          <el-input v-model="order.id" disabled></el-input>
        </el-form-item>
        <el-form-item label="用户" prop="this.username">
          <el-input v-model="this.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="剧目" prop="order.video">
          <el-input v-model="order.video" disabled></el-input>
        </el-form-item>
        <el-form-item label="演出厅" prop="order.studio">
          <el-input v-model="order.studio" disabled></el-input>
        </el-form-item>
        <el-form-item label="座位" prop="order.tickets">
          <span v-for="(item, index) in this.order.tickets" :key="index"
            >{{ item.ticketRows }}行{{ item.ticketColumns }}列</span
          >
          <!-- <el-input v-model="order.tickets" disabled></el-input> -->
        </el-form-item>
        <el-form-item label="价格" prop="order.price">
          <el-input v-model="order.price" disabled></el-input>
        </el-form-item>
        <el-form-item label="时间" prop="order.time">
          <el-input v-model="order.time" disabled></el-input>
        </el-form-item>
        <el-button class="one" type="primary" @click="submitPay()"
          >支付</el-button
        >
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  name: "PayMent",
  data() {
    return {
      username: window.localStorage.getItem("username"),
      order: {
        id: "",
        time: "",
        video: "",
        studio: "",
        tickets: [],
        uid: "",
        orderStatus: "",
        price: "",
      },
    };
  },
  created() {
    // console.log(window.localStorage.getItem("order"));
    this.order = JSON.parse(window.localStorage.getItem("order"));
    console.log(this.order);
  },
  methods: {
    async submitPay() {
      const { data: res } = await this.$http.post("/ticket/updateTicket/pay", {
        token: window.localStorage.getItem("token"),
        value: {
          id: this.order.id,
        },
        uid: this.order.uid,
      });
      console.log(res);
      this.$router.push("/MyOrder");
    },
  },
};
</script>
<style scoped>
.updateForm {
  width: 400px;
  height: 560px;
  margin: 30px auto;
  border-radius: 15px;
  background-clip: padding-box;
  padding: 15px 35px 15px 35px;
  box-sizing: border-box;
  background: aliceblue;
  border: 1px solid blueviolet;
  box-shadow: 0 0 25px #f885ff;
}
.el-button {
  width: 100px;
  text-align: center;
  border-radius: 5px;
  margin: 10px 0;
}
h3 {
  margin: 10px auto;
  text-align: center;
}
.show {
  width: 60%;
  /* margin: 0 auto; */
  padding: 20px;
}
.show .el-descriptions-item {
  padding: 20px;
  margin: 20px;
}
</style>