// 未完成
<template>
  <div>
    <div class="notHava" v-show="notHave">暂无订单</div>
    <div class="hava" v-show="!notHave">
      <el-card v-for="(item, index) in order" :key="index">
        <el-descriptions :title="item.time" :colon="false">
          <el-descriptions-item label="订单号ID"
            >: {{ item.id }}</el-descriptions-item
          >
          <el-descriptions-item label="剧目"
            >《 {{ item.video }}》</el-descriptions-item
          >
          <el-descriptions-item label="时间"
            >: {{ item.time }}</el-descriptions-item
          >
          <el-descriptions-item label="座位"
            >:
            <span v-for="(items, index) in item.tickets" :key="index"
              >{{ items.ticketRows }}行{{ items.ticketColumns }}列</span
            ></el-descriptions-item
          >
          <el-descriptions-item label="金额"
            >: {{ item.price }}</el-descriptions-item
          >
          <el-descriptions-item
            label="订单号状态"
            v-if="item.orderStatus == 'Paid'"
            >: 已支付
          </el-descriptions-item>
          <el-descriptions-item
            label="订单号状态"
            v-if="item.orderStatus == 'unsole'"
            >: 暂未支付
          </el-descriptions-item>
          <el-descriptions-item
            label="订单号状态"
            v-if="item.orderStatus == 'CancelOrder'"
            >: 已取消
          </el-descriptions-item>
          <el-descriptions-item
            label="订单号状态"
            v-if="item.orderStatus == 'Use'"
            >: 已使用
          </el-descriptions-item>
        </el-descriptions>
        <div
          class="btns"
          v-if="item.orderStatus == 'Paid' || item.orderStatus == 'Locking'"
        >
          <el-button @click="cancle(item.id)">取消该订单</el-button>
        </div>
      </el-card>
    </div>
  </div>
</template>
<script>
export default {
  name: "MyOrder",
  data() {
    return {
      notHave: false,
      orderStatus1: "Paid",
      orderStatus2: "unSole",
      orderStatus3: "Locking",
      order: [],
    };
  },
  async created() {
    // 获取订单数据请求
    const { data: res } = await this.$http.get("/ticket/getOrderByUid", {
      params: {
        token: window.localStorage.getItem("token"),
        uid: window.localStorage.getItem("id"),
      },
    });
    console.log(res.value);
    this.order = res.value;
  },
  methods: {
    async cancle(id) {
      const { data: rest } = await this.$http.post(
        "/ticket/updateTicket/refund",
        {
          token: window.localStorage.getItem("token"),
          value: {
            id: id,
          },
          uid: window.localStorage.getItem("id"),
        }
      );
      console.log(rest);
      const { data: res } = await this.$http.get("/ticket/getOrderByUid", {
        params: {
          token: window.localStorage.getItem("token"),
          uid: window.localStorage.getItem("id"),
        },
      });
      console.log(res.value);
      this.order = res.value;
    },
  },
};
</script>
<style scoped>
.el-button {
  width: 100px;
  margin-right: 50px;
}
span {
  display: inline-block;
  margin-right: 5px;
  padding: 2px;
  /* background-color: chartreuse; */
  background-color: black;
  color: #fff;
}
.el-card {
  width: 98%;
  margin: 20px auto;
  padding-left: 20px;
}
</style>